-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)}to{width:0;height:0;opacity:0;top:50vh}}@-webkit-keyframes cap_rise{from{top:-100vh}to{top:0}}@keyframes cap_rise{from{top:-100vh}to{top:0}}@-webkit-keyframes cap_remove{from{top:0}to{top:100vh}}@keyframes cap_remove{from{top:0}to{top:100vh}}.preloader{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden;display:flex;justify-content:center;align-items:center}.preloader__cap_element{position:absolute;top:0;height:100%;background-color:#323232}.preloader__raised_cap_element{-webkit-animation-name:cap_rise;animation-name:cap_rise;-webkit-animation-duration:800ms;animation-duration:800ms;-webkit-animation-fill-mode:both;animation-fill-mode:both}.preloader__removed_cap_element{-webkit-animation-name:cap_remove;animation-name:cap_remove;-webkit-animation-duration:800ms;animation-duration:800ms;-webkit-animation-fill-mode:both;animation-fill-mode:both}.preloader__pulsar{border:2px solid whitesmoke;width:3em;height:3em;border-radius:50%;position:relative}.preloader__rised_pulsar{-webkit-animation-name:pulsar_rise;animation-name:pulsar_rise;-webkit-animation-duration:800ms;animation-duration:800ms}.preloader__jumped_pulsar{-webkit-animation-name:pulsar_jump;animation-name:pulsar_jump;-webkit-animation-duration:700ms;animation-duration:700ms;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.preloader__removed_pulsar{-webkit-animation-name:pulsar_remove;animation-name:pulsar_remove;-webkit-animation-duration:250ms;animation-duration:250ms;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}

